:root {
  --fs-1: 3.13em;
  --fs-2: 2.5em;
  --fs-3: 1.56em;
  --fs-4: 1.56em;
  --fs-5: .75em;
  --fs-6: 1em;
  --fs-7: .63em;
}

@media screen and (min-width: 35.5em) {
  :root {
    --fs-1: 6.25em;
    --fs-2: 3.13em;
    --fs-3: 1.94em;
    --fs-4: 1.56em;
    --fs-5: 1.25em;
    --fs-6: .81em;
    --fs-7: .63em;
  }
}

.header-primary {
  display: none;
}

.header-primary h1 {
  margin-block: .5em;
}

.sign-container {
  flex-direction: column;
  gap: 2em;
  margin-bottom: 1em;
  display: flex;
}

.header-block header {
  position: relative;
}

.header-block header h2 {
  white-space: nowrap;
  color: #201d1f;
  text-align: center;
  background-color: #fff;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
  padding: 0 .5em;
  font-size: 2.5em;
  font-weight: 600;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)translateY(-40%);
}

.header-block__body {
  box-sizing: border-box;
  border: 2px solid #201d1f;
  flex-direction: column;
  height: 100%;
  padding: 2.5em;
  display: flex;
}

.header-block__body form {
  flex-direction: column;
  align-items: flex-start;
  gap: 1em;
  display: flex;
}

.header-block__body form .form-control {
  width: 100%;
}

.header-block__body form .btn, .header-block__body form .link {
  align-self: center;
}

@media screen and (min-width: 35.5em) {
  .header-primary {
    display: block;
  }

  .sign-container {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    display: grid;
  }

  .header-block header h2 {
    white-space: nowrap;
  }

  .header-block .header-block__body {
    padding: 5em;
  }

  .header-block__body form .btn, .header-block__body form .link {
    align-self: flex-start;
  }
}

