.header-primary {
  display: block;
}

.sign-container {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.header-block {
  header {
    h2 {
      white-space: nowrap;
    }
  }
  .header-block__body {
    padding: 5em;
  }
}

.header-block__body {
  form {
    .btn,
    .link {
      align-self: flex-start;
    }
  }
}
