@import "variables";

.header-primary {
  display: none;
  h1 {
    margin-block: 0.5em;
  }
}

.sign-container {
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-bottom: 1em;
}

.header-block {
  header {
    position: relative;
    h2 {
      position: absolute;
      width: fit-content;
      margin: 0;
      white-space: nowrap;
      top: 0em;
      left: 50%;
      transform: translateX(-50%) translateY(-40%);
      padding: 0 0.5em;
      background-color: white;

      color: $black;
      font-size: 2.5em;
      font-weight: 600;
      text-align: center;
    }
  }
}

.header-block__body {
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 2.5em;
  border: 2px solid $black;

  form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1em;
    .form-control {
      width: 100%;
    }
    .btn,
    .link {
      align-self: center;
    }
  }
}

@include sm {
  @import "sign-in-sm";
}
@include lg {
  @import "sign-in-lg";
}
